<script>
	export default {
		data() {
			return {
				list: {
					'1': null
				},
				isLoading: false,
				code: '',
				loop:null,
			}
		},
		onLoad(obj) {
			this.code = obj.code
			console.log(obj)
			this.isLoading = false
			this.queryHandle()
			if(!this.loop){
				this.loop = setInterval(()=>{
					this.queryHandle()
				},3000)
			}
			
		},
		onBackPress(){
			
			clearInterval(this.loop)
			console.log(",,,",this.loop)
		},
		methods: {
			queryHandle() {
				this.isLoading = true
				let _this = this
				this.request({
					url: 'https://quote.xyzq.cn/utcloud/level2/' + this.code,
					method: 'get',
					//contentType:'application/json',
					data: Object.assign({

					}),
				}).then(data => {
					_this.isLoading = false
					if (data) {

						_this.list = data[_this.code]
						//console.log('data',_this.list)
						_this.list['7'] = (parseInt(data[_this.code]['7']) / 1000000).toFixed(2) + '万手'
						_this.list['8'] = (parseInt(data[_this.code]['8']) / 100000000).toFixed(2) + '亿'
					} else {
						uni.showToast({
							title: "获取数据失败",
							icon: "none"
						});
					}
				});


			}
		}
	}
</script>
